<template>
  <div>
    <div class="section">
      <div class="matter">
        <p class="title">技能类别</p>
        <div class="edit">
          <p class="text">{{service.name}}</p>
        </div>
      </div>
      <div class="matter">
        <p class="title">技能名称</p>
        <div class="edit">
          <p class="text">{{name}}</p>
        </div>
      </div>
    </div>

    <div class="section">
      <div class="matter">
        <p class="title">技能售价</p>
        <div class="edit">
          <p class="text">{{price}}</p>
          <span class="unit">元/小时</span>
        </div>
      </div>
    </div>

    <div class="section">
      <div class="matter show">
        <p class="title">服务日期</p>
        <div class="edit">
          <p class="text ellipsis">
              <span v-if="!workDay.timeless && workDay.startTime == workDay.endTime">{{workDay.startTime}}</span>
              <span v-if="!workDay.timeless && workDay.startTime != workDay.endTime">{{workDay.startTime}}-{{workDay.endTime}}</span>
              <span v-if="workDay.timeless">长期有效</span>

              <span v-if="workDay.startTime.length > 0 || workDay.timeless">({{workDay.split | week-format}})</span>
          </p>
        </div>
      </div>
      <div class="matter show mid">
        <p class="title">服务时间</p>
        <div class="edit">
          <p class="text">{{workTime}}</p>
        </div>
      </div>
    </div>

    <div class="section">
      <div class="matter">
        <p class="title">服务范围</p>
        <div class="edit">
          <p class="text ellipsis">{{workArea | area-filter}}</p>
        </div>
      </div>
      <div class="matter show mid">
        <p class="title">服务地址</p>
        <div class="edit">
          <p class="text">{{address}}</p>
        </div>
      </div>
    </div>

    <div class="section">
      <div class="matter textarea">
        <p class="title">技能介绍</p>
      </div>
      <div class="edit more">
        <p class="text">{{intro}}</p>
      </div>
    </div>

    <div class="section">
      <div class="matter textarea">
        <p class="title">服务说明</p>
      </div>
      <div class="edit more">
        <p class="text">{{description}}</p>
      </div>
    </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import {toast} from '../assets/js/toast'
  import {url} from '../const'

  export default{
    ready(){
      let vm = this;
      //注册事件
      vm.$on('reload-child',vm.loadPublish);
      vm.$emit('reload-child');
      
      vm.$on('destroy-publish', vm.destroyPublish);
      vm.$on('publish', vm.doPublish);
      vm.$on('cancel-publish', vm.cancelPublish);
      vm.$on('save-publish', vm.savePublish);
    }
    ,
    data()
    {
      return {
        id: 0,
        service: {name: '', serviceId: 0},
        name: '',
        price: '',
        workDay: {startTime: '', endTime: '', split: ''},
        workTime: '',
        workArea: '',
        intro: '',
        description: '',
        address: '',
        status: 0
      }
    },
    methods: {
      loadPublish(){
        let vm = this;
        let query = vm.$route.params;
        toast.loading();
        vm.$http.get(url.publishDetail, {id: query.id}).then(
          function (response) {
            let res = response.data;
            let success = res.success;
            let data = res.data;
            if (success) {
              let name = data.subCategoryName;
              let serviceId = data.subCategory;
              let str = serviceId + "";
              if (str.substring((str.length - 2), str.length) == "99") {
                name = data.categoryName + '-其他';
              }
              let p = {
                id: data.id,
                service: {serviceId: serviceId, name: name},
                name: data.name,
                price: data.price,
                workDay: {startTime: data.startTime, endTime: data.endTime, split: data.workSplit,timeless:data.timeless},
                workTime: data.workTime,
                workArea: data.workArea,
                intro: data.intro,
                description: data.description,
                address: data.address,
                status: data.status,
                getTime: parseInt(new Date().getTime() / 1000)
              };
              vm.pushEdit(p);
              vm.$dispatch('publish-status', this.status);//发送状态
            } else {
              toast.alert(resultMsg);
            }
          }, function (response) {
            toast.alert('网络异常');
          }
        );        
      },
      pushEdit(news){
        this.id = news.id;
        this.service = news.service;
        this.name = news.name;
        this.price = news.price;
        this.workDay = news.workDay;
        this.workTime = news.workTime;
        this.workArea = news.workArea;
        this.intro = news.intro;
        this.description = news.description;
        this.address = news.address;
        this.status = news.status;
      },
      doPublish(){
        let vm = this;
        console.log('show ...');
        vm.$http.get(url.changeStatus, {id: this.id, status: 1}).then(
          function ({data}) {
            if (data.success) {
              toast.alert('发布成功');
              vm.$dispatch('publish-status', 1);
            } else {
              toast.alert(data.resultMsg);
            }
          }
        )
      },
      cancelPublish(){
        let vm = this;
        toast.conform('确定取消?', function () {
          vm.$http.get(url.changeStatus, {id: vm.id, status: 0}).then(
            function ({data}) {
              if (data && data.success) {
                toast.alert('取消成功');
                vm.$dispatch('publish-status', 0);
              } else {
                toast.alert('取消失败');
              }
            }
          )
        });
      },
      destroyPublish(){
        let vm = this;
        toast.conform('确定删除?', function () {
          vm.$http.get(url.changeStatus, {id: vm.id, status: 3}).then(
            function ({data}) {
              if (data && data.success) {
                toast.alert('删除成功');
                window.history.back();
              } else {
                toast.alert('删除失败');
              }
            }
          )
        });
      },
      editData(status){
        let vm = this;
        let postData = {
          id: this.id,
          subCategory: vm.service.serviceId,
          name: vm.name,
          price: vm.price,
          startTime: vm.workDay.startTime,
          endTime: vm.workDay.endTime,
          workTime: vm.workTime,
          validWeek: vm.workDay.split,
          workArea: vm.workArea,
          intro: vm.intro,
          description: vm.description,
          address: vm.address,
          status: status
        }
        return postData;
      }
    }
    ,
    computed: {}
  }
</script>
<style scoped>
  .section .matter {
    font-size: .14rem;
  }

  .matter.more {
    height: auto;
  }

  .matter.show {
    height: .68rem;
  }

  .matter.mid {
    height: .8rem;
  }

  .matter.textarea {
    height: .3rem;
    border-bottom: none;
  }

  .matter.show .edit {
    width: 100%;
    line-height: 1;
    height: auto;
  }

  .matter.mid .edit {
    line-height: 1.2;
  }

  .edit .unit {
    position: absolute;
    right: .1rem;
    top: 0;
    color: #ffb600;
  }

  .matter .edit-area {
    height: .8rem;
    line-height: 1.4;
    width: 100%;
    margin-top: .1rem;
  }

  .edit.more {
    min-height: .8rem;
    width: 100%;
    margin-top: .1rem;
    padding: 0 .1rem .1rem;
    line-height: 1.4;
    clear: both;
  }

  .edit .text {
    color: #333;
  }

  .edit .text.none {
    color: #999;
  }

  .edit input[type='text'] {
    width: 100%;
    text-indent: 0;
    padding: 0;
  }

  .show .title, .textarea .title {
    height: auto;
  }
</style>



