<template>
  <div>
    <v-header>
      <div slot="right" class="h-right">
        <a v-link="{name:'publish-edit',params:{id:id}}" v-if="status !=2"  class="mg-r10">编辑</a>
      </div>
    </v-header>

    <v-content :type="'form'">
      <v-publish-detail class="publish-detail">
      </v-publish-detail>
    </v-content>

    <v-footer-btn>
      <input type="button" value="删除" :class="['input',status==2 ? 'max':'small','destroy']" v-if="status == 0 || status == 2" @click="destroyPublish"/>
      <input type="button" value="发布" :class="['input','mid','ok','right']" v-if="status == 0" @click="publish"/>
      <input type="button" value="取消发布" :class="['input','max','cancel']" v-if="status == 1" @click="cancelPublish"/>

    </v-footer-btn>
  </div>
</template>
<script>
  import VHeader from '../../components/Header'
  import VContent from '../../components/Content'
  import VFooterBtn from '../../components/FooterBtn'
  import VPublishDetail from '../../components/PublishShow'

  export default{
    route:{    
      data({from,to}){
        if(this.isReady){
          return {
            state:!this.state
          }
        }    
      }
    },     
    ready(){
      let vm = this;
      vm.id = vm.$route.params.id;
      console.log('show.vue id =' + vm.id);
      this.isReady = true;
    },
    data(){
      return {
        isReady:false,
        state:false,     
        
        id:0,
        status: 0
      }
    },
    methods: {
      save(){
        this.$broadcast('save-publish');
      },
      publish(){
        this.$broadcast('publish');
      },
      destroyPublish(){
        this.$broadcast('destroy-publish');
      },
      cancelPublish(){
        this.$broadcast('cancel-publish');
      }
    },
    events: {
      'publish-status': function (status) {
        this.status = status;
      }
    },
    watch:{
      'state':function(){
        this.id = this.$route.params.id;
        this.$broadcast('reload-child');
      }
    },    
    components: {VHeader, VContent, VFooterBtn, VPublishDetail},
  }
</script>





